body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Main page formatting */
.app {
  text-align: center;
}

.app-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: 'Montserrat', sans-serif;
  font-size: calc(10px + 2vmin);
  color: black;
}

.body {
  font-size:110%;
  font-family: 'Montserrat', sans-serif;
  display: -webkit-flex;
  display: flex;
}

.button {
  border-radius: 8px;
  margin: .5em;
}

/* Card formatting */
.bookImage {
  padding: 1em;
}

.bookCard {
  margin: .5em;
  width: 350px;
  height: 300px;
  display: -webkit-flex;
  display: flex;
  background-color: #3EB6C5;
  border-radius: 12px;
}

.bookInfo {
  margin: .5em 0 0 0;
  padding: 0 .3em;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  text-align: left;
}

.bookResults {
  padding: 0 0 0 .5em;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.authors {
  padding: .25em 0 .25em 0;
  font-style: italic;
}

.title {
  -webkit-font-feature-settings: "c2sc";
          font-feature-settings: "c2sc";
  font-variant: small-caps;
  font-weight: bold;
  font-size: 1.5em;
}

.publisher {
  padding: .25em 0 .25em 0;
}
